body {
  background-color: #383635;
  /* background-color: #EDEEEF; */
  color: #e0e0e0;
  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans',
    'Helvetica Neue', sans-serif; */
  font-family: "Helvetica Neue";
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 800px;
  margin: auto;
  padding: 30px;
}

.button-container {
  display: block;
  text-align: center;
}

.button-container2 {
  display: block;
  text-align: left;
  margin-top: 5px;
}

.button-container Button,  .button-container2 Button{
  background-color: #e0e0e0;
  color: #383635;
  border-color: #e0e0e0;
  font-weight: 600;
}

.button-container Button:hover, .button-container2 Button:hover {
  background-color: #383635;
  color: #e0e0e0;
  border-color: #e0e0e0;
}

/* .field-container .textfield {
  background-color: #e0e0e0;
} */

.textfield {
  border-radius: 50px;
}

.MuiTextField-root {
  background-color: #e0e0e0;
  border-radius: none;
}

.rc-button {
  cursor: pointer;
  padding: 10px;
  margin: 10px;
  border-radius: 10px;
  background-color: #f0d9b5;
  border: none;
}
.rc-button:hover,
.selected {
  background-color: #b58863;
}

*::-webkit-scrollbar {
  width: 10px;
  background-color: var(--secondary-bg);
}

*::-webkit-scrollbar-thumb {
  border-radius: 5px;
  background-color: #0f0f0f;
}

.board {
  font-size: 100px;
}

.divider{
  width:20px;
  height:auto;
  display:inline-block;
}

.players {
  margin: 0;
  font-size: 30px;
}

#possible-moves {
  display: block;
  max-width: 700px;
  /* background-color: yellow; */
}

#possible-moves td {
  display: inline-block;
  margin-right: 7px;
}

.best-move {
  text-decoration: underline;
}

/* ******** Start of Nav ******** */

a:link {
  text-decoration: none;
}

a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
  color: white !important;
}

a:active {
  text-decoration: none;
  color: blue;
}

ul {
  list-style-type: none;
}

nav {
  width: 100%;
  text-decoration: none;
  /* background-color: #524F4E; */
  /* background-color: #b58863; */
  background-color: #f0d9b5;
  /* background-color: #EDEEEF; */
  font-weight: 700;
}

nav ul {
  display: flex;
  justify-content: center;
  gap: 50px;
}

img {
  height: 1100px;
  width: 850px;
}

.gif {
  height: inherit;
  width: 300px;
}

#gif-container {
  display: flex;
  height: 300px;
  width:750px;
  justify-content: space-around;
}
